import React, {Component} from "react";
import {PropTypes} from "prop-types";
import {Section, Trigger} from "eshop/modules/core/components/ui/Expander";
import {LCol, LRow} from "./Utils";
import UniversalProductDetails from "./FixProductDetails";
import {GraphicCell, NameCell, PriceCell} from "./modalFragments/FixInputFragments";
import Outline from "../../../../../core/enum/Outline"

export default class FixProductCheckBox extends Component {
    constructor(props){
        super(props);
    }

    getSectionHeader() {
        return (
            <Trigger className="js-expander__trigger__nested u-inline-block large-offset-by-3 medium-offset-by-3 u-padding-all-l u-no-padding-top">
                <span className="js-expander__trigger--show">{'Zwiń\u00a0szczegóły'}</span>
                <span className="js-expander__trigger--hide">{'Szczegóły'}</span>
            </Trigger>
        );
    }

    render(){
        let outlineStyle = {}
        if (!!this.props.vas.warningMessage) {
            outlineStyle = { outline: Outline.WARN };
        } else if (this.props.vas.borderDescription){
            outlineStyle = {outline: Outline.INFO};
        }
        let onClick = {}
        if (this.props.vas.selected && !!this.props.vas.onRemoveWarning){
            onClick = this.props.onClickWrapped(this.props.vas.id, this.props.vas.propositionId, this.props.vas.onRemoveWarning);
        } else {
            onClick = this.props.onSelectionChanged(this.props.vas.id, this.props.vas.propositionId)
        }
        let borderDescriptionHtml = { __html: this.props.vas.borderDescription };
        const hasBorder = this.props.vas.warningMessage || this.props.vas.borderDescription;
        return (
                <div className={`u-position-relative u-margin-top-m ${!hasBorder ? 'u-position-relative g-white1-bg is-selected' : ''}`}
                    style={{...outlineStyle, marginTop: '2px'}}>
                    {
                        this.props.vas.warningMessage &&
                        <div className="opl-box opl-payback-box opl-content-arrow--no-border g-yellowf-bg u-padding-m u-padding-top-m">
                            <div className="u-no-margin l-row">
                                <span className="g-icon g-icon--only g-icon--warn g-icon--xs-s u-padding u-padding-right-s" aria-hidden="true"/>
                                <span className="u-strong h5">Uwaga!</span>
                                <p className="u-no-margin">{this.props.vas.warningMessage}</p>
                            </div>
                            <div className="u-no-margin-bottom l-row">
                                <div className="l-col l-col-3 large-offset-by-6 medium-offset-by-6">
                                    <div className="opl-content-arrow__object opl-content-arrow--no-border g-yellowf-b-bg" style={{marginTop:"-5px", zIndex:"-1"}}/>
                                </div>
                            </div>
                        </div>
                    }
                    {
                        this.props.vas.borderDescription && !this.props.vas.selected &&
                        <div className="opl-box opl-payback-box opl-content-arrow--no-border g-bluef-bg u-padding-m u-padding-top-m">
                            <div className="u-no-margin l-row">
                                <p className="h4 u-no-margin g-white1-c" dangerouslySetInnerHTML={borderDescriptionHtml}/>
                            </div>
                            <div className="u-no-margin-bottom l-row">
                                <div className="l-col l-col-3 large-offset-by-6 medium-offset-by-6">
                                    <div className="opl-content-arrow__object opl-content-arrow--no-border g-bluef-b-bg" style={{marginTop:"-10px", zIndex:"-1"}}/>
                                </div>
                            </div>
                        </div>
                    }

                        {!hasBorder && this.props.vas.selected &&
                            <div className="opl-selection-layer" aria-hidden="true">
                                <div className="opl-selection-layer__border"></div>
                                <div className="opl-selection-layer__checkmark"></div>
                            </div>
                        }

                        <LRow id={`product-checkbox-row-${this.props.vas.id}-${this.props.idx}`} className="u-no-margin u-padding-all-l u-no-padding-bottom u-small-padding-all u-small-padding-top-l"
                            onClick={onClick}>
                            <GraphicCell vas={this.props.vas}/>
                            <NameCell vas={this.props.vas}/>
                            <PriceCell vas={this.props.vas} net={this.props.net}/>
                            <LCol small="12" medium="2" big="2" className="u-small-position-absolute u-small-position-right u-small-w-auto u-small-padding-right-l">
                                <label className="opl-switch u-right">
                                    <input type="checkbox"
                                        className="u-acc-hide"
                                        disabled={this.props.vas.mandatory}
                                        checked={this.props.vas.selected}
                                        onChange={() => null}/>
                                    <span className="o-ci"></span>
                                    <span className="o-ci-label">
                                        <span className="u-acc-txt--show">Wybieram</span>
                                        <span className="u-acc-txt--hide">Wybrane</span>
                                    </span>
                                </label>
                            </LCol>
                        </LRow>
                        <Section header={this.getSectionHeader()} contentClassName="js-expander__container__nested" headerClassName="u-position-relative u-small-text-right" className="l-row u-no-margin opl-section-outer--2">
                            <UniversalProductDetails vas={this.props.vas}/>
                        </Section>
                    </div>
            );
    }
}

FixProductCheckBox.propTypes = {
    vas: PropTypes.shape({
        title: PropTypes.string,
        id: PropTypes.string,
        paymentDescriptions: PropTypes.shape({
            monthlyPayments: PropTypes.array
        }),
        warningMessage: PropTypes.string,
        onRemoveWarning: PropTypes.string
    }),
    idx: PropTypes.oneOfType([PropTypes.string,PropTypes.number]),
    onSelectionChanged: PropTypes.func,
    onClickWrapped: PropTypes.func
}
